.fade-enter {
  opacity: .1; }

.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity .3s linear;
  transition: opacity .3s linear; }

.fade-exit {
  opacity: 1; }

.fade-exit-active {
  opacity: .1;
  -webkit-transition: opacity .3s linear;
  transition: opacity .3s linear; }

body {
  font-size: 16px;
  line-height: 25px;
  font-family: 'Lato', sans-serif;
  color: #000; }

.container {
  max-width: 915px;
  margin: 0 auto;
  padding: 0 15px; }

.header {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 20px;
  align-items: center;
  padding: 20px 0; }
  .header .logo {
    font-family: 'Lexend Exa', sans-serif;
    font-size: 30px; }
    .header .logo a {
      display: block; }
    .header .logo__text {
      display: inline-block;
      color: #010101; }

.footer {
  padding: 20px 0;
  font-family: 'Lexend Exa', sans-serif; }
  .footer__copyright {
    color: gray;
    font-size: 14px;
    text-align: center; }

.movie {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 20px;
  margin-bottom: 20px; }
  .movie__title {
    margin-bottom: 10px; }
  .movie__category {
    color: rgba(0, 0, 0, 0.6);
    font-size: 14px;
    margin-bottom: 10px; }
  .movie__date {
    color: rgba(0, 0, 0, 0.6);
    font-size: 14px; }
  .movie__description {
    display: -webkit-box;
    max-height: 50px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2; }
  .movie__readmore {
    position: relative;
    display: block;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin-left: auto;
    color: #2b66ec;
    overflow: hidden;
    text-decoration: none; }
    .movie__readmore:before {
      content: "";
      background-color: #2b66ec;
      position: absolute;
      width: 100%;
      height: 1px;
      left: 0;
      bottom: 0;
      -webkit-transition: all 0.3s ease;
      transition: all 0.3s ease; }
    .movie__readmore:hover:before {
      left: -100%; }

.movie-list {
  display: grid;
  grid-template-columns: 1fr; }

.search {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-gap: 20px; }
  .search__input {
    width: 100%;
    border: 0;
    border-bottom: 1px solid #000; }
  .search__button {
    background-color: transparent;
    color: #000;
    border: 2px solid #000; }
    .search__button:hover {
      background-color: #000;
      color: #fff; }

.load-more {
  display: block;
  margin: 0 auto;
  padding: 0 10px;
  background-color: transparent;
  border: 2px solid #000;
  color: #000; }
  .load-more:hover {
    background-color: #000;
    color: #fff; }

.movie-details-block-left {
  margin-bottom: 20px; }

.movie-details__poster {
  max-width: 100%; }

.movie-details__title {
  margin-bottom: 20px; }
  .movie-details__title a {
    color: #2b66ec;
    text-decoration: none; }

.movie-details-row {
  display: flex;
  flex-wrap: wrap;
  align-items: center; }
  .movie-details-row.flex-diraction-column {
    flex-direction: column;
    align-items: flex-start; }
  .movie-details-row__title {
    margin-right: 10px;
    font-weight: 600; }

.movie-details__rate {
  display: flex;
  justify-content: center;
  align-items: center; }

.movie-details-rate__total-line {
  position: relative;
  width: 100px;
  height: 10px;
  background-color: rgba(0, 255, 0, 0.25); }

.movie-details-rate__line {
  position: absolute;
  left: 0;
  width: 0%;
  height: 100%;
  background-color: lime;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease; }

.movie-details-rate__amount {
  margin-left: 10px; }

.movie-details__backto {
  position: relative;
  display: block;
  margin-bottom: 10px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  color: #2b66ec;
  overflow: hidden;
  text-decoration: none; }
  .movie-details__backto:before {
    content: "";
    background-color: #2b66ec;
    position: absolute;
    width: 100%;
    height: 1px;
    left: 0;
    bottom: 0;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease; }
  .movie-details__backto:hover:before {
    left: -100%; }

.movie-details__cast-block {
  margin-top: 20px; }
  .movie-details__cast-block-title {
    font-size: 20px;
    margin-bottom: 20px; }

.movie-details__cast-list {
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-gap: 10px; }

.cast__img {
  max-width: 100%; }

.cast__name {
  font-weight: 600; }

.preloader {
  position: relative;
  width: 100%;
  height: calc(100vh - 150px); }
  .preloader__inner {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    height: 50px;
    width: 50px;
    margin: -25px 0 0 -25px; }
    .preloader__inner span {
      position: absolute;
      width: 10px;
      height: 10px;
      background: rgba(0, 0, 0, 0.25);
      border-radius: 50%;
      -webkit-animation: animation-dots 1s infinite ease-in-out;
      animation: animation-dots 1s infinite ease-in-out; }
      .preloader__inner span:nth-child(1) {
        left: 0px;
        -webkit-animation-delay: 0.2s;
        animation-delay: 0.2s; }
      .preloader__inner span:nth-child(2) {
        left: 15px;
        -webkit-animation-delay: 0.3s;
        animation-delay: 0.3s; }
      .preloader__inner span:nth-child(3) {
        left: 30px;
        -webkit-animation-delay: 0.4s;
        animation-delay: 0.4s; }
      .preloader__inner span:nth-child(4) {
        left: 45px;
        -webkit-animation-delay: 0.5s;
        animation-delay: 0.5s; }

@-webkit-keyframes animation-dots {
  0% {
    -webkit-transform: translateY(0px);
      -webkit-transform-transform: translateY(0px);
    background: #d62d20; }
  25% {
    -webkit-transform: translateY(10px);
      -webkit-transform-transform: translateY(10px);
    background: #ffa700; }
  50% {
    -webkit-transform: translateY(10px);
      -webkit-transform-transform: translateY(10px);
    background: #008744; }
  100% {
    -webkit-transform: translateY(0px);
      -webkit-transform-transform: translateY(0px);
    background: #0057e7; } }

@keyframes animation-dots {
  0% {
    -webkit-transform: translateY(0px);
      -webkit-transform-transform: translateY(0px);
    -webkit-transform: translateY(0px);
      -webkit-transform-transform: translateY(0px);
    background: #d62d20; }
  25% {
    -webkit-transform: translateY(10px);
      -webkit-transform-transform: translateY(10px);
    -webkit-transform: translateY(10px);
      -webkit-transform-transform: translateY(10px);
    background: #ffa700; }
  50% {
    -webkit-transform: translateY(10px);
      -webkit-transform-transform: translateY(10px);
    -webkit-transform: translateY(10px);
      -webkit-transform-transform: translateY(10px);
    background: #008744; }
  100% {
    -webkit-transform: translateY(0px);
      -webkit-transform-transform: translateY(0px);
    -webkit-transform: translateY(0px);
      -webkit-transform-transform: translateY(0px);
    background: #0057e7; } }

